* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
}

body{
}

::-webkit-scrollbar {
    width: 1px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(255, 255, 255); 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 255, 255); 
  }
.login_card{
    width: 400px;
    height: 100%;
    border-radius: 10px;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    margin: 5rem auto;
    padding: 1rem;
    background-color: rgba(255, 255, 255, 0.151);
}



.product_container{
    /* display: flex;
    flex-direction: row; */
    margin: 1rem 2rem;
    /* justify-content: space-between;
    align-items: center; */
}



.side_bar{
    
    margin-right: 1rem;
    /* border: 2px solid red; */
    border-radius: 10px;
    /* width: 20%; */
    height: 100vh;
    /* display: inline-block; */
    float: left;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 4rem 2rem;
    line-height: 4rem;
    position: fixed;
    top: 0rem;
    
}

.side_bar div{
    cursor: pointer;
    opacity: 0.7;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all .3s ease-in-out;
}

.res_item{
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all .3s ease-in-out;
}

.res_item:hover{
    opacity: 1;
    font-weight: bold;
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
}


.side_bar div:hover{
    opacity: 1;
    font-weight: bold;
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
}

.product {
    /* border: 2px solid red; */
    width: 80%;
    height: 100%;
    /* margin: 1rem; */
    /* display: inline-block; */
    float: right;
    border-radius: 10px;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    padding: 0rem 2rem;
}

.All_product_div{
    /* width: 500px; */
    /* width: 100%; */
    /* display: flex; */
    /* justify-content: stretch; */
    margin: 1rem 2rem;
}

.cart_btn{
    position: fixed;
    bottom: 1rem;
    right: 2rem;
    z-index: 100;
}

@media (max-width:700px){

    .side_bar{
        display: none;
    }

    .product{
        width: 100%;
        float: none;
        padding: 0 0.5rem;
    }

    .product_container{
        margin: 1rem 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .All_product_div{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .res_div2{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

}
