.link{
    text-decoration: none;
}

.page_img img{
    width: 19rem;
}

.page_img{
    position: relative;
    left: 0;
    -webkit-animation: gaurav 3s ease-in-out 0s infinite forwards;
            animation: gaurav 3s ease-in-out 0s infinite forwards;
}

@-webkit-keyframes gaurav {
    0%{
        left: -2rem;
    }
    25%{            
        left: 4rem;
    }
    /* 75%{
        left: 4rem;
    } */
    100%{
        left: -2rem;
    }
}

@keyframes gaurav {
    0%{
        left: -2rem;
    }
    25%{            
        left: 4rem;
    }
    /* 75%{
        left: 4rem;
    } */
    100%{
        left: -2rem;
    }
}



@media (max-width:700px) {
    .log_main{
        flex-direction: column-reverse;
    }

    .info_div{
        width: 100%;
        text-align: center;
    }

    .btn_div{
